@import "lib";
// Shetland's configuration for bootstrap.

// Background color for `<body>`.
$body-bg: $colourBackground;
// Prefer a more contrasty text.
$text-color: $colourText;
$brand-primary: $colourArtfulBrightBlue;

// Fonts
$font-family-sans-serif:  "Lato", Helvetica, Arial, sans-serif;

// Bootstrap build {{{
// The following is required so that the boostrap-provided files can resolve references
$icon-font-path: '~bootstrap-sass/assets/fonts/bootstrap/';
$icon-font-path: '../node_modules/bootstrap-sass/assets/fonts/bootstrap/';

// Without this, bootstrap thinks all labels should be white, which makes them hard to read on white backgrounds...
$label-color: inherit;


// Namespace bootstrap under body.shetland-theme (I think Civi adds this class)

.shetland-theme {
  /*!
   * Bootstrap v3.4.1 (https://getbootstrap.com/)
   * Copyright 2011-2019 Twitter, Inc.
   * Licensed under MIT (https://github.com/twbs/bootstrap/blob/master/LICENSE)
   */

  // Core variables and mixins
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/variables";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/mixins";

  // Reset and dependencies
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/normalize";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/print";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/glyphicons";

  // Core CSS
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/scaffolding";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/type";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/code";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/grid";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/tables";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/forms";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/buttons";

  // Components
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/component-animations";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/dropdowns";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/button-groups";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/input-groups";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/navs";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/navbar";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/breadcrumbs";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/pagination";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/pager";
  @import "~bootstrap-sass/assets/stylesheets/bootstrap/labels";
